  /* iOS 4.1- */
}
.iconfont {
  font-family: "iconfont" !important;
  font-size: 16px;
  font-style: normal;
  -webkit-font-smoothing: antialiased;
  -webkit-text-stroke-width: 0.2px;
  -moz-osx-font-smoothing: grayscale;
}
*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
body {
  background-color: #e0e0e0;
  font-family: "lucida grande", "lucida sans unicode", lucida, helvetica, "Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
  -webkit-font-smoothing: antialiased;
}
a {
  transition: color 200ms ease;
  text-decoration: none;
  color: #437a7e;
}
a:hover {
  color: #ee8781!important;
}
.main-container {
  position: relative;
  overflow: hidden;
  width: 1024px;
  padding: 40px 55px;
  background-color: #fff;
  border-radius: 4px;
  margin: 20px auto;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
}
.line {
  margin: 10px 0;
  background-color: #3C6066;
  height: 5px;
  border-radius: 2px;
}
.line.small {
  height: 3px;
}
.subfield {
  position: relative;
  overflow: hidden;
}
.subfield h1 {
  float: left;
  margin: 5px 0 0 15px;
  font-size: 24px;
}
.subfield h3 {
  margin: 5px 0 0 0;
  font-size: 18px;
  display: inline-block;
}
.subfield h4 {
  margin: 0;
  font-size: 18px;
  display: inline-block;
  color: #3c6066;
}
.subfield .items {
  margin: 0;
  list-style: none;
  font-size: 15px;
}
.subfield .items li {
  margin-bottom: 3px;
  padding-left: 16px;
}
.subfield .items:before {
  content: " ";
  display: block;
  position: absolute;
  width: 3px;
  left: 25px;
  height: 100%;
  background-color: #3C6066;
}
.subfield .left,
.subfield .right {
  position: relative;
  width: 50%;
  float: left;
}
.sec-title {
  overflow: hidden;
}
.sec-title .underline {
  margin-top: 42px;
  margin-left: 60px;
  margin-right: 20px;
  height: 2px;
  background-color: #3c6066;
}
.sec-title:before {
  font-family: iconfont;
  font-size: inherit;
  text-rendering: auto;
  transform: translate(0, 0);
  background-color: #54979b;
  color: #f9fafc;
  border-radius: 50%;
  float: left;
  width: 52px;
  height: 52px;
  padding: 8px;
  text-align: center;
  line-height: 36px;
  font-size: 28px;
  z-index: 1;
}
.items .sub-items {
  list-style: none;
  padding: 0;
}
.items .sub-items li {
  padding-left: 1.1em;
}
.browsehappy {
  text-align: center;
  margin: 0;
  background: #fff8e2;
  color: #000;
  padding: 0.5em 0;
}
._icon {
  border: 1px solid #437a7e;
  border-radius: 3px;
  display: inline-block;
  padding: 0 3px;
  font-size: 14px;
}
._icon:hover {
  color: #ee8781;
  border-color: #ee8781;
}
.icon-demo {
  border: 1px solid #437a7e;
  border-radius: 3px;
  display: inline-block;
  padding: 0 3px;
  font-size: 14px;
}
.icon-demo:hover {
  color: #ee8781;
  border-color: #ee8781;
}
.mark {
  color: #006565;
  font-weight: bold;
}
.pdf-download {
  font-size: 20px;
  color: white;
  background-color: #1b2032;
  position: fixed;
  bottom: 5px;
  right: 5px;
  border-radius: 5px;
  padding: 2px 5px;
}
header {
  overflow: hidden;
}
.contact {
  float: left;
  list-style: none;
  margin: 0;
  padding-left: 10px;
}
.contact li {
  font-size: 18px;
  margin-bottom: 5px;
}
.icon {
  font-size: 20px;
}
.title {
  float: right;
  text-align: right;
}
.title h1 {
  font-size: 70px;
  margin: 0;
}
.title h2 {
  font-size: 25px;
  margin: 0;
  font-weight: 100;
  color: #54979b;
}
.basic .sec-title:before {
  content: "\e606";
}
.basic .indent {
  text-indent: 1em;
}
.skill .sec-title:before {
  content: "\e608";
}
.work .sec-title:before {
  content: "\e607";
}
